<template>
  <view class="call-view">
    <!-- 本地视频画面 -->
    <grtc-video v-if="call.mediaType === 1" :userId="call.caller.id" class="main-video"></grtc-video>

    <view class="profile">
      <image class="user-avatar" mode="widthFix" :src="callee.data.avatar"></image>
      <text class="user-name">{{callee.data.name}}</text>
    </view>	
    <view class="status-box">
      <text class="status-notice">等待对方接受邀请</text>
      <image src="https://wangzhenghai-oss.oss-cn-hangzhou.aliyuncs.com/04dfd54b-f131-49f6-aee9-1ef8f988a8cd.gif" class="loading"></image>
    </view>

    <view class="action-box">
      <view class="action-item" @click="end()">
        <view class="end-background">
          <image class="action-icon" src="https://wangzhenghai-oss.oss-cn-hangzhou.aliyuncs.com/8af91edd-40d2-4bbb-8a5c-caef7457bb07.png"></image>
        </view>
        <text class="action-text">取消</text>
      </view>
    </view>

  </view>
</template>

<script>
  const GRTC = uni.$GRTC;
  export default {
    data() {
      return {
        call: GRTC.currentCall(),
        callee: GRTC.currentCall().callees[0],
      }
    },
    onLoad() {
      this.initListener();
    },
    onUnload() {
      GRTC.off(GRTC.EVENT.USER_ACCEPTED, this.onUserAccepted);
      GRTC.off(GRTC.EVENT.USER_RANG, this.onUserRang);
      GRTC.off(GRTC.EVENT.USER_QUIT, this.onUserQuit);
    },
    onBackPress(event) {
      return event.from === 'backbutton';  // 禁止安卓侧滑返回
    },
    methods: {
      initListener() {
        GRTC.on(GRTC.EVENT.USER_ACCEPTED, this.onUserAccepted);
        GRTC.on(GRTC.EVENT.USER_RANG, this.onUserRang);
        GRTC.on(GRTC.EVENT.USER_QUIT, this.onUserQuit);
      },
      onUserAccepted(event) {
        plus.nativeUI.toast(event.user.data.name+"已接听");
        uni.redirectTo({
          url: './call'
        });
      },
      onUserRang(event) {
        plus.nativeUI.toast(event.user.data.name+"已响铃");
      },
      onUserQuit(event) {
        const username = event.user.data.name;
        let message = '';
        switch (event.reason) {
          case 'DIAL_TIMEOUT':
            message = `拨打超时`;
            break;
          case 'CANCELLED':
            message = `已取消`;
            break;
          case 'REJECTED':
            message = `${username}已拒绝`;
            break;
          case 'GOEASY_DISCONNECTED':
            message = `${username}网络异常`;
            break;
        }
        plus.nativeUI.toast(message);
        uni.navigateBack();
      },
      end() {
        GRTC.end();
      },
    }
  }
</script>

<style scoped>
@import url('/static/style/rtcCallStyle.css');
</style>
